}

:root{
  --background: #fff;
  --text-primary: #141414;
}
[data-theme='dark']{
  --background: rgb(53, 53, 53);
  --text-primary: #fff;
}

.darkMode{
  background-color: rgb(53, 53, 53);
}
:root {
  --red: hsl(0, 78%, 62%);
  --cyan: hsl(180, 62%, 55%);
  --orange: hsl(34, 97%, 64%);
  --blue: hsl(212, 86%, 64%);
  --varyDarkBlue: hsl(234, 12%, 34%);
  --grayishBlue: hsl(229, 6%, 66%);
  --veryLightGray: hsl(0, 0%, 98%);
  --weight1: 200;
  --weight2: 400;
  --weight3: 600;
  --black: #141414;
}
body{
    font-family: "Georgia, 'Times New Roman', Times, serif";
}
body::-webkit-scrollbar {
    display: none;
}

.navbar {
    margin: 0;
    padding: 0;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 20;
    border-bottom: solid black 2px;
    background-color: #fff;
  }
  
  .nav-container {
    display: flex;
    height: 100%;
    align-items: center;
    width: 100%;
    max-width: 1500px;
  }
  .nav-item-cart{
    position: relative;
  }
  
  .nav-logo {
    color: #141414;
    color: var(--black);
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2.6rem;
    flex-grow: 1;
    font-family: "Parisienne";
    letter-spacing: 4px;
  }
  .nav-SignIn{
    background: transparent;
    width: 120px;
    padding: 10px 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    color: #141414;
    border-radius: 10px;
    border: 2px solid rgba(0, 0, 0, 0.712);
    transition: all 0.3s ease-in-out;
  }
  .nav-SignIn:hover{
    color: #fff;
    background: #141414b9;
  }
  .nav-logo:hover{
    color: #eb2a2a;
  }
  
  .nav-menu {
    display: flex;
    list-style: none;
    margin-right: 2rem;
    margin-bottom: 0;
    width: 100%;
    justify-content: center;
    align-items: center;
  }
  
  .nav-links {
    color: #141414;
    color: var(--black);
    font-weight: bold;
    font-size: 25px;
    font-family: "Poiret One";
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
    border-bottom: 3px solid transparent;
    transition: 0.5s;
  }
  .nav-links-user {
    color: #141414;
    color: var(--black);
    font-weight: 500;
    font-size: 25px;
    font-family: "Bodoni Moda";
    text-decoration: none;
    padding: 0.8rem 1rem;
    border-bottom: 3px solid transparent;
  }
  .nav-links-user:hover{
    color: #eb2a2a;
  }
  .fa-code {
    margin-left: 1rem;
  }
  .nav-item-user {
    line-height: 40px;
    margin-right: 1rem;
  }
  .nav-item {
    line-height: 40px;
    margin-right: 1rem;
  }
  .nav-item-logo{
    flex-grow: 2;
    text-align: center;
  }
  .on{
    background: rgb(252, 252, 252);
    box-shadow: 0px 1px 20px 0px rgb(0, 0, 0);
    transition: all 0.2s ease-in-out;
  }
  .nav-links:hover{
    color: #eb2a2a;
  }
  
  
  .nav-icon {
    display: none;
  }
  .cart-length{
    position: relative;
  }
  .badge{
    background: red;
    position: absolute;
    top:0;
    right: 5px;
    padding: 0.1rem 0.3rem;
    color: #fff;
    border-radius: 50%;
    font-size: 0.8rem;
  }
  .test-div{
    display: none;
  }
  
  
  
  @media screen and (max-width: 960px) {
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      border-top: 1px solid #fff;
      position: absolute;
      top: 90px;
      left: -110%;
      opacity: 0;
      transition: all 0.5s ease;
      margin-top: -16px;
    }
    .nav-item{
      padding: 20px 0;
    }
    .nav-container {
      display: flex;
      align-items: center;
      margin: 0;
      padding: 0 20px;
      width: 100%;
      height: 80px;
      max-width: 1500px;
      justify-content: flex-end;
    }
    .nav-item-user{
      display: flex;
      margin: 0;
      padding: 0;
      width: 100%;
      height: 100%;
      justify-content: center;
      align-items: center;
    }
    .nav-links-user{
      padding: 0;
    }
    .nav-icon {
      display: flex;
      width: 100%;
      height: 100%;
      font-size: 1.8rem;
      cursor: pointer;
      color: #141414;
      justify-content: flex-start;
      align-items: center;
    }
    .nav-logo{
      display: none;
    }
  
    .nav-menu.active {
      background: #fff;
      left: 0px;
      top: 80px;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
      box-shadow: 0px 10px 10px 0px rgb(0, 0, 0);
    }
    
    .nav-item .active {
      color: #141414;
      border: none;
    }
    .nav-item-cart .nav-links {
      width: 100%;
      margin: 0;
      padding: 0;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      
    }
    .nav-item-cart{
      width: 100%;
      margin: 0;
      padding: 0;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      margin-right: 20px;
    }

    .badge{
      background: red;
      position: absolute;
      top: -5px;
      right: -5px;
      padding: 0.1rem 0.2rem;
      color: #fff;
      border-radius: 50%;
      font-size: 0.8rem;
    }
  }
  @media screen and (max-width: 600px) {
    .nav-item-cart{
      margin-right: 10px;
    }
  }
  @media screen and (max-width: 400px) {
    .nav-item-cart{
      margin-right: 0px;
    }
  }
.menu-dropdown{
    background: rgb(252, 252, 252);
    border-radius: 10px;
    margin-left: -120px;
    width: 200px;
    position: absolute;
    list-style: none;
    padding: 0px 10px;
    top: 100px;
    box-shadow: 0px 0px 10px 1px black;
    opacity: 0;
    transition: 0.5s;
    pointer-events: none;
  }
  .active{
    opacity: 1;
    pointer-events: visible;
    top: 70px;
    transition: 0.5s;
  }
  
  
  .menu-dropdown li {
    background: transparent;
    cursor: pointer;
    padding: 5px 0;
    border-top: 1px solid rgba(0,0,0,0.05);
  }
  
  .menu-dropdown li:hover {
    background: transparent;
  }
  
  .dropdown-link {
    display: block;
    height: 100%;
    width: 100%;
    text-decoration: none;
    color: #141414;
    padding: 10px;
    display: flex;
    align-items: center;
    transition: 0.5s;
  }
  .dropdown-link .fa-sign-out-alt{
    transform: rotate(180deg);
  }
  .dropdown-link i{
    margin-right: 20px;
    opacity: 0.5;
    transition: 0.5s;
  }
  .dropdown-link:hover i{
    opacity: 1;
  }
  .dropdown-link:hover{
    color: #eb2a2a;
  }
  .dropdown-link:hover i{
    color: #141414;
    opacity: 1;
  }
  .dropdown-link-name{
    display: block;
    text-decoration: none;
    font-weight: bold;
    color: #141414;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: "Parisienne";
    font-size: 25px;
    pointer-events: none;
    cursor: none;
  }
  .dropdown-link i{
      margin-left: 10px;
  }
  .menu-dropdown .link-darkMode{
    display: flex;
    justify-content: center;
  }
  .tumbler__wrapper{
    width: 50px;
    height: 30px;
    background: rgb(252, 252, 252);
    box-shadow: 0px 0px 3px 0px black;
    border: darken(#aae2f1, 40);
    border-radius: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 6px;
    cursor: pointer;
    display: flex;
    position: relative;
  }
  .tumbler__wrapper i{
      width: 15px;
      height: 15px;
  }
  .tumbler
    {
    position: absolute;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background-color: rgb(83, 83, 83);
    transition: transform .5s, background-color .5s;
    will-change: transform;
  }
  .tumbler__wrapper .fa-moon{
      color: rgb(114, 114, 114);
      font-size: 14px;
      margin-top: 1.5px;
  }
  .tumbler__wrapper .fa-sun{
    color: rgb(114, 114, 114);
    font-size: 14px;
    margin-top: 2px;
  }
  .tumbler-dark{
    transform: translateX(calc(100% - 2px))
  }

  .dark{
    background-color: rgb(53, 53, 53);
    color: #fff;
  }
  .dark-link{
    color: #fff;
  }
  .item-active{
    color: #eb2a2a;
  }
  
  @media screen and (max-width: 960px) {
    .fa-caret-down {
      display: none;
    }
    .menu-dropdown{
      margin-left: -10px;
    }
  }
