.container-details{
    height: 90vh;
}
.row-details{
    font-family: "PT Serif";
    display: flex;
    height: 100%;
    width: 100%;
    align-items: center;
}
.img-box-details{
    flex-grow: 2;
    height: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}
.img-box-details img{
    height: 100%;
}
.details-box-details{
    flex-basis: 40%;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding-left: 150px;
}
.details-box-details h1{
    font-family: "Oswald";
    color: #141414;
    font-size: 60px;
    font-weight: bold;
    text-align: start;
    margin: 20px 0;
}
.details-box-details h2{
    font-family: "Bodoni Moda";
