.buttons{
    display: flex;
}

.buttons .add{
    background: transparent;
    color: #141414;
    border: 2px solid #141414;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    padding: 10px 20px;
}

.buttons .add:hover{
    background: #14141467;
    border: 2px solid #14141467;
    color: white;
}

.buttons button:hover{
    background: #2ad6b1;
    color: white;
}

.buttons .remove{
    background: transparent;
    border-radius: 0;
    color: #d31515;
    border: 2px solid #d31515;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    padding: 10px 20px;
}


.buttons .remove:hover{
    background: #ad1414;
    color: white;
}
.buttons .edit{
    background: transparent;
    color: #374eb3;
    border: 2px solid #374eb3;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    padding: 10px 20px;
    margin: 0 5px;
}
.buttons .edit:hover{
    background: #374eb3;
    color: white;
}
@media screen and (max-width: 960px) {
