  display: flex;
  padding-top: 20px;
  flex-direction: column;
  align-items: center;
  font-family: "Bodoni Moda";
  position: absolute;
  background: rgb(252, 252, 252);
  padding-right: 20px;
  border-radius: 200px;
  z-index: 3;
}
.header-text h1{
  font-weight: 500;
  text-align: center;
  font-size: 80px;
  color: #141414d3;
}
.header-text h2{
    font-family: "Bodoni Moda";
    font-weight: 100;
    color: #263238;
    font-style: italic;
    font-size: 30px;
}
.header-text .none{
    text-decoration: none;
}
.header-svg{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    z-index: 0;
}
.header-svg img{
    height: 100%;
    box-shadow: 5px 5px 40px 0px black;
}

.body-home{
    font-family: Helvetica, Arial, sans-serif;
    font-size: 14px;
    color:#000;
    padding: 0;
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
}

.container-sec1{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.name{
    align-self: center;
}
.name h1{
    font-size: 70px;
    text-shadow: 2px 2px #000000;
    color: #30e0ba;
    text-align: center;
}
.text-p{
    align-self: center;
    height: 80px;
}
.text-p p{
    font-size: 35px;
    color: rgb(73, 73, 73);
    font-style: italic;
    text-align: center;
}
.buttons-home{
    align-self: center;
    border-radius: 5px;
}
.buttons-home .none{
    text-decoration: none;
}
.titel{
    margin: 50px 0;
    display: flex;
    flex-direction: column;
    
}
.main{
    display: flex;
}
.main .main-text{
    color: #141414d3;
    font-size: 60px;
    font-weight: 400;
}
.main .main-option{
    margin-left: -10px;
    height: 90px;
    line-height: 90px;
    overflow: hidden;
}
.main-option li{
    list-style: none;
    color: #eb2a2a;
    font-size: 60px;
    font-weight: 500;
    position: relative;
    top: 0;
    animation: slide 12s steps(1) infinite;
}
@keyframes slide{
    100%
    {
        top: -360px;
    }
}
.main-option li span{
    position: relative;
    margin: 5px 0;
    line-height: 90px;
}
.main-option li span:after{
    content: '';
    position: absolute;
    left: 0;
    width: 100%;
    height: 120%;
    background: rgb(252, 252, 252);
    border-left: 2px solid #eb2a2a;
    animation: typing 3s steps(10) infinite;
}
@keyframes typing{
    40%, 60%{
        left: calc(100% + 30px);
    }
    100%{
        left:0;
    }
}

.noselect {
    -webkit-touch-callout: none;
      -webkit-user-select: none;
          -ms-user-select: none;
              user-select: none;
          -webkit-tap-highlight-color: transparent;
  }
  
  .noselect {
      width: 150px;
      height: 20px;
      cursor: pointer;
      display: flex;
      align-items: center;
      background: transparent;
      border: none;
      
  }
  
  .noselect, .noselect span {
      transition: 200ms;
  }
  
  .noselect .text {
      transform: translateX(40px);
      color: #141414;
      font-weight: bold;
  }
  
  .noselect .icon {
      position: absolute;
      transform: translateX(100px);
      height: 40px;
      width: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      
  }
  
  .noselect .icon i {
      width: 15px;
      color: #141414;
      display: none;
  }
  
  .noselect:hover .text {
      color: transparent;
  }
  
  .noselect:hover .icon {
      width: 150px;
      border-left: none;
      transform: translateX(-5px);
  }
  .noselect:hover .icon i{
    display: inline;
  }
  
  .noselect:focus {
      outline: none;
  }

